import {Injectable} from '@angular/core';
import {environment} from '../../../environments/environment';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {ApiModel} from '../../shared/model/api.model';

@Injectable({
    providedIn: 'root'
})
export class CategoriseServices {

    constructor(private http: HttpClient) {
    }

    public getCategoriseForAdmin(meta?: string) {
        const url = meta ? environment.MAIN_SYSTEM + 'api/dashboard/categories/get-categories-for-admin?' + meta : environment.MAIN_SYSTEM + 'api/dashboard/categories/get-categories-for-admin';
        return this.http.get<ApiModel<any>>(url);
    }

    public getCategorise(meta?: string) {
        const url = meta ? environment.MAIN_SYSTEM + 'api/dashboard/categories?' + meta : environment.MAIN_SYSTEM + 'api/dashboard/categories';
        return this.http.get<ApiModel<any>>(url);
    }

    public getCategoriesByLevel( level : string, meta?:string ) {
        var url = environment.MAIN_SYSTEM + 'api/dashboard/categories?level='+level;
        url = meta ? url+'&' + meta : url;
        return this.http.get<ApiModel<any>>(url);
    }

    public getCategoriesAllWithTree() {
        const url =  environment.MAIN_SYSTEM + 'api/dashboard/categories/get-all-main-sub-child';
        return this.http.get<ApiModel<any>>(url);
    }

    public getParentCategorise(meta?: string) {
        const url = meta ? environment.MAIN_SYSTEM + 'api/dashboard/categories/parent-and-sub?' + meta : environment.MAIN_SYSTEM + 'api/dashboard/categories/parent-and-sub';
        return this.http.get<ApiModel<any>>(url);
    }

    public CreateCategory(newCategory) {
        const url = environment.MAIN_SYSTEM + 'api/dashboard/categories';
        return this.http.post(url, newCategory);
    }

    public getCategory(categoryId) {
        const url = environment.MAIN_SYSTEM + 'api/dashboard/categories/' + categoryId;
        return this.http.get(url);
    }

    public editCategory(category) {
        const url = environment.MAIN_SYSTEM + 'api/dashboard/categories/update';
        return this.http.post(url, category);
    }

    public deleteCategory(categoryId) {
        const options = {
            headers: new HttpHeaders({}),
            body: {
                id: categoryId
            }
        };
        const url = environment.MAIN_SYSTEM + 'api/dashboard/categories';
        return this.http.delete(url, options);
    }


    public selectedCategories(filter) {
        const url = environment.MAIN_SYSTEM + 'api/dashboard/categories/list/' + filter;
        return this.http.get(url);
    }

    public getCategoriesForSelection() {
        const url = environment.MAIN_SYSTEM + 'api/dashboard/categories/for-selection';
        return this.http.get(url);
    }

}
