import {Injectable} from '@angular/core';
import {environment} from '../../../environments/environment';
import {HttpClient, HttpEvent, HttpHeaders, HttpRequest} from '@angular/common/http';

@Injectable({
    providedIn: 'root'
})
export class SharedApiServiceService {

    constructor(private http: HttpClient) {
    }

    exportUrl: any;

    getCities(meta?: any) {
        const url = meta ? environment.MAIN_SYSTEM + 'api/dashboard/cities/for-selection?' + meta : environment.MAIN_SYSTEM + 'api/dashboard/cities/for-selection';
        return this.http.get<any>(url);
    }

    exportData(targetUrl) {
        const url = environment.MAIN_SYSTEM + 'api/dashboard/' + targetUrl;
        const opts: any = {
            headers: new HttpHeaders({
                'Accept': 'text/html, application/xhtml+xml, */*',
                'Content-Type': 'text/plain; charset=utf-8'
            }),
            responseType: 'blob'
        };
        return this.http.get<string>(url, opts);
    }
}
