import {Injectable} from '@angular/core';
import {CompositeFilterDescriptor, FilterDescriptor, State} from '@progress/kendo-data-query';

@Injectable({
    providedIn: 'root'
})
export class FilterToQueryService {
    queryString: string = '';

    constructor() {
    }

    convertToQuery(state: State): string {
        let fieldName = '';
        let filter: Array<FilterDescriptor | CompositeFilterDescriptor> = state.filter?.filters ? state.filter.filters : [];
        this.queryString = '';
        filter.forEach((item: FilterDescriptor, i) => {
            // if (item.field === 'name_en' || item.field === 'name_ar') {
            //     fieldName = 'name';
            // } else {
            fieldName = item.field.toString();
            // }
            this.queryString = this.queryString + ((item.value || item.value === false) ? (fieldName + '=' + item.value + '&') : '');
        });
        this.queryString = this.queryString + 'limit=' + state.take + '&offset=' + state.skip;
        return this.queryString;
    }
}
